@import "../App/variables.scss";

.navigationsWrapper {
  display: flex;
  align-items: center;

  .navigations_item {
      padding: 10px 20px;
      text-decoration: none;
      color: black;


    &:hover {
      border-bottom: 1px solid $green;
      color: $green
    }
  }
}


@media screen and (max-width: 720px) {
  .navigationsWrapper {
    display: none;
  }
}